.education {
    padding: 80px 0;
    background: linear-gradient(135deg, #0a0a0a 0%, #1a1a1a 100%);
    border-top: 1px solid rgba(255, 107, 53, 0.1);
  }
  
  .education-content {
    max-width: 1000px;
    margin: 0 auto;
  }
  
  .section-title {
    display: flex;
    align-items: center;
    gap: 1rem;
    font-size: 2.5rem;
    font-weight: 600;
    color: #ffffff;
    margin-bottom: 3rem;
    text-align: center;
    justify-content: center;
  }
  
  .orange-line {
    width: 4px;
    height: 40px;
    background-color: #ff6b35;
    border-radius: 2px;
  }
  
  .education-timeline {
    display: flex;
    flex-direction: column;
    gap: 3rem;
    position: relative;
  }
  
  
  .education-item {
    display: flex;
    align-items: flex-start;
    gap: 2rem;
    position: relative;
    margin-bottom: 2rem;
  }
  
  .education-icon {
    width: 80px;
    height: 80px;
    background: rgba(255, 107, 53, 0.1);
    border: 2px solid #ff6b35;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #ff6b35;
    font-size: 2rem;
    flex-shrink: 0;
    position: relative;
    z-index: 2;
  }
  
  .education-content-item {
    flex: 1;
    background: rgba(255, 255, 255, 0.02);
    border: 1px solid rgba(255, 255, 255, 0.05);
    border-radius: 12px;
    padding: 2rem;
    transition: all 0.3s ease;
  }
  
  .education-content-item:hover {
    background: rgba(255, 107, 53, 0.05);
    border-color: rgba(255, 107, 53, 0.2);
    transform: translateY(-5px);
  }
  
  .education-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
  }
  
  .education-degree {
    font-size: 1.5rem;
    font-weight: 600;
    color: #ffffff;
    margin: 0;
  }
  
  .education-grade {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    color: #ff6b35;
    font-weight: 500;
  }
  
  .grade-icon {
    font-size: 1rem;
  }
  
  .education-institution {
    font-size: 1.2rem;
    font-weight: 500;
    color: #ff6b35;
    margin-bottom: 0.8rem;
  }
  
  .education-duration {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    color: #cccccc;
    font-size: 1rem;
    margin-bottom: 1rem;
  }
  
  .duration-icon {
    color: #ff6b35;
    font-size: 0.9rem;
  }
  
  .education-description {
    color: #cccccc;
    font-size: 1rem;
    line-height: 1.6;
    margin: 0;
  }
  
  @media (max-width: 768px) {
    .education {
      padding: 60px 0;
    }
  
    .section-title {
      font-size: 2rem;
    }
  
    .education-timeline::before {
      left: 30px;
    }
  
    .education-item {
      flex-direction: row !important;
      gap: 1rem;
    }
  
    .education-icon {
      width: 60px;
      height: 60px;
      font-size: 1.5rem;
    }
  
    .education-content-item {
      padding: 1.5rem;
    }
  
    .education-header {
      flex-direction: column;
      align-items: flex-start;
      gap: 0.5rem;
    }
  
    .education-degree {
      font-size: 1.3rem;
    }
  
    .education-institution {
      font-size: 1.1rem;
    }
  }
  
  @media (max-width: 480px) {
    .education-timeline::before {
      left: 20px;
    }
  
    .education-icon {
      width: 50px;
      height: 50px;
      font-size: 1.2rem;
    }
  
    .education-content-item {
      padding: 1rem;
    }
  
    .education-degree {
      font-size: 1.2rem;
    }
  }